.preloader {
  height: 100%;
  width: 100%;
  z-index: 99999;
  background: #1e2d3b;
  display: table;
  position: fixed;
  top: 0;
  left: 0;
}

.preloader .loader_34 {
  vertical-align: middle;
  text-align: center;
  display: table-cell;
}

.preloader .loader_34 .ytp-spinner {
  width: 64px;
  z-index: 18;
  pointer-events: none;
  margin-left: -32px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.preloader .loader_34 .ytp-spinner .ytp-spinner-container {
  pointer-events: none;
  width: 100%;
  margin-top: -50%;
  margin-left: -50%;
  padding-bottom: 100%;
  animation: ytp-spinner-linspin 1.56824s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

.preloader .loader_34 .ytp-spinner .ytp-spinner-container .ytp-spinner-rotator {
  width: 100%;
  height: 100%;
  animation: ytp-spinner-easespin 5.332s cubic-bezier(.4, 0, .2, 1) infinite both;
  position: absolute;
}

.preloader .loader_34 .ytp-spinner .ytp-spinner-container .ytp-spinner-rotator .ytp-spinner-left {
  position: absolute;
  inset: 0 50% 0 0;
  overflow: hidden;
}

.preloader .loader_34 .ytp-spinner .ytp-spinner-container .ytp-spinner-rotator .ytp-spinner-right {
  position: absolute;
  inset: 0 0 0 50%;
  overflow: hidden;
}

.preloader .loader_34 .ytp-spinner-circle {
  box-sizing: border-box;
  width: 200%;
  height: 100%;
  border: 6px solid #754ef9;
  border-bottom-color: #ddd;
  border-radius: 50%;
  position: absolute;
}

.preloader .loader_34 .ytp-spinner-left .ytp-spinner-circle {
  border-right-color: #ddd;
  animation: ytp-spinner-left-spin 1.333s cubic-bezier(.4, 0, .2, 1) infinite both;
  left: 0;
  right: -100%;
}

.preloader .loader_34 .ytp-spinner-right .ytp-spinner-circle {
  border-left-color: #ddd;
  animation: ytp-right-spin 1.333s cubic-bezier(.4, 0, .2, 1) infinite both;
  left: -100%;
  right: 0;
}

@keyframes ytp-spinner-linspin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes ytp-spinner-easespin {
  12.5% {
    transform: rotate(135deg);
  }

  25% {
    transform: rotate(270deg);
  }

  37.5% {
    transform: rotate(405deg);
  }

  50% {
    transform: rotate(540deg);
  }

  62.5% {
    transform: rotate(675deg);
  }

  75% {
    transform: rotate(810deg);
  }

  87.5% {
    transform: rotate(945deg);
  }

  to {
    transform: rotate(1080deg);
  }
}

@keyframes ytp-spinner-left-spin {
  0% {
    transform: rotate(130deg);
  }

  50% {
    transform: rotate(-5deg);
  }

  to {
    transform: rotate(130deg);
  }
}

@keyframes ytp-right-spin {
  0% {
    transform: rotate(-130deg);
  }

  50% {
    transform: rotate(5deg);
  }

  to {
    transform: rotate(-130deg);
  }
}

